<template>
<v-app>
    <v-container fluid pa-0>
        <v-row align="center" justify="center" style="height:85vh" dense>
            <v-col cols="12" lg="6" md="6" class="darkbg fill-height d-none d-lg-flex flex-column justify-center align-center">
                <v-card flat tile>
                    <v-img src="@/assets/Karnataka.png" alt="Karnataka" class="darkbg" max-height="85vh" contain></v-img>
                </v-card>
            </v-col>
            <v-col cols="12" lg="6" md="12" class="darkbg fill-height d-flex flex-column justify-center align-center">
                <v-card flat tile>
                    <v-img src="@/assets/2020_hiking.jpg" alt="Karnataka" class="darkbg" max-height="75vh" contain></v-img>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
    <v-container fluid pa-0>
        <v-row dense justify="center" class="my-10">
            <v-col justify="left" align="left" cols="10" lg="9" xl="8">
                <div class="display-1 pl-2" style="border-bottom: 2px solid #475767; "> Upcomming Events </div>
            </v-col>
        </v-row>
        <v-row dense justify="center" class="my-10">
            <v-col justify="left" align="left" cols="10" lg="9" xl="8">
                <v-row class="pl-5 mb-8" v-for="(event, index) in next_events" :key="index">
                    <v-card tile>
                        <v-card-title class="mt-n2 justify-center headline font-weight-bold text--secondary">{{ getDisplayDate(event.date) }}</v-card-title>
                        <v-card-subtitle class="mb-n2 justify-center"> {{ getMonthYear(event.date) }}</v-card-subtitle>
                    </v-card>
                    <v-card tile class="ml-lg-2 event" flat @click="eventSelected = index; eventType = 'next'; dialog = true">
                        <v-card-title class="mt-n2 justify-center">{{ event.title }}</v-card-title>
                        <v-card-subtitle class="mb-n2 justify-center">
                            <v-icon class="mt-n1" small>mdi-map-marker-outline</v-icon> {{ event.place }}
                        </v-card-subtitle>
                    </v-card>
                </v-row>
            </v-col>
        </v-row>
    </v-container>

    <v-container fluid pa-0>
        <v-row dense justify="center" class="my-10">
            <v-col justify="left" align="left" cols="10" lg="9" xl="8">
                <div class="display-1 pl-2" style="border-bottom: 2px solid #475767; "> Past Events </div>
            </v-col>
        </v-row>
        <v-row dense justify="center" class="my-10">
            <v-col justify="left" align="left" cols="10" lg="9" xl="8">
                <v-row class="pl-5 mb-8" v-for="(event, index) in past_events" :key="index">
                    <v-card tile>
                        <v-card-title class="mt-n2 justify-center headline font-weight-bold text--secondary">{{ getDisplayDate(event.date) }}</v-card-title>
                        <v-card-subtitle class="mb-n2 justify-center"> {{ getMonthYear(event.date) }}</v-card-subtitle>
                    </v-card>
                    <v-card tile class="ml-lg-2 event" flat @click="eventSelected = index; eventType = 'past'; dialog = true">
                        <v-card-title class="mt-n2 justify-center">{{ event.title }}</v-card-title>
                        <v-card-subtitle class="mb-n2 justify-center">
                            <v-icon class="mt-n1" small>mdi-map-marker-outline</v-icon> {{ event.place }}
                        </v-card-subtitle>
                    </v-card>
                </v-row>
            </v-col>
        </v-row>
    </v-container>
    <v-dialog v-model="dialog" width="1000">
        <v-card>
            <div class="d-block text-right">
                <v-btn icon class="mx-2 mt-2" @click="dialog = false">
                    <v-icon>mdi-close-box</v-icon>
                </v-btn>
            </div>
            <div class="d-flex ml-10 pt-2">
                <span class="title mr-2"> When: </span>
                <span class="title font-weight-regular">{{ eventType == 'next' ? next_events[eventSelected].date : past_events[eventSelected].date }} </span>
            </div>
            <div class="d-flex ml-10 pt-2">
                <span class="title mr-2"> What: </span>
                <span class="title font-weight-regular"> {{ eventType == 'next' ? next_events[eventSelected].title : past_events[eventSelected].title }} </span>
            </div>
            <div class="d-flex ml-10 pt-2">
                <span class="title mr-2"> Where: </span>
                <span class="title font-weight-regular"> {{ eventType == 'next' ? next_events[eventSelected].place : past_events[eventSelected].place }} </span>
            </div>
            <div class="d-flex ml-10 pt-2">
                <span class="title mr-2"> Description: </span>
            </div>
            <div class="d-flex ml-10 pt-2 pb-8">
                <span class="subtitle-1 font-weight-regular ml-2 mr-10 text-left" v-html="eventType == 'next' ? next_events[eventSelected].description : past_events[eventSelected].description"> </span>
            </div>

        </v-card>
    </v-dialog>
</v-app>
</template>

<script>
import events from '@/events/current_year.json'
export default {
    name: "Home",
    components: {},
    data() {
        return {
            dialog: false,
            eventSelected: 0,
            eventType: "next",
            next_events: [{
                date: "2020-10-03",
                title: "This is the string",
                place: "This is the string",
                description: "This can be html"
            }],
            past_events: []
        }
    },
    methods: {
        getDisplayDate: (dt) => {
            return new Date(dt).getDate().toString().padStart(2, 0)
        },
        getMonthYear: (dt) => {
            let monthName = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"]
            return monthName[new Date(dt).getMonth()] + " " + new Date(dt).getFullYear().toString().substring(2, 4)
        }
    },
    mounted() {
        this.next_events = [];
        this.past_events = [];
        events.forEach(event => {
            if (new Date() < new Date(event.date))
                this.next_events.push(event)
            else
                this.past_events.push(event)
        })
    }
};
</script>

<style scoped>
.darkbg {
    background-color: #262626 !important;
}

.event:hover {
    background-color: #c5c5c5 !important;
    cursor: pointer;
}
</style>
